import React, { useEffect } from "react"
import fetch from "node-fetch"
import Home from "./home"
import Aboutme from "./aboutme"
import Skill from "./skill"
import Experience from "./experience"
import Education from "./education"
import Contact from "./contact"
import Firebase from "../config/firebaseConfig"
import { Footer } from "../components"
import MobileDetect from "mobile-detect"
import Project from "./project"
import { ApiKey } from "../config/apiConfig"

function Pages() {
  const year = new Date().getFullYear()
  const month = new Date().getMonth() + 1
  const day = new Date().getDate()
  const database = Firebase.database()
    .ref()
    .child("test")
    .child(year)
    .child(month)
    .child(day)

  const md = new MobileDetect(window.navigator.userAgent)

  useEffect(() => getLocation())

  function getLocation() {
    if (navigator.geolocation) {
      navigator.geolocation.getCurrentPosition(showPosition, showError)
    } else {
      database.push([
        new Date().toString(),
        "Geolocation is not supported by this browser."
      ])
    }
  }

  function showPosition(position) {
    const latlon = position.coords.latitude + "," + position.coords.longitude
    database.push({ date: new Date().toString(), geolocation: latlon })
    //       date: new Date().toString(),
    // return fetch("https://geo.ipify.org/api/v1?apiKey=" + ApiKey)
    //   .then(res => res.json())
    //   .then(json =>
    //     database.push({
    //       date: new Date().toString(),
    //       ip: json.ip,
    //       isp: json.isp,
    //       user: md.ua,
    //       geolocation: latlon,
    //       from:
    //         json.location["city"] +
    //         json.location["region"] +
    //         json.location["country"],
    //       iplocation: json.location["lat"] + ", " + json.location["lng"]
    //     })
    //   )
  }

  function showError(error) {
    switch (error.code) {
      case error.PERMISSION_DENIED:
        fetch("https://geo.ipify.org/api/v1?apiKey=" + ApiKey)
          .then(res => res.json())
          .then(json =>
            database.push({
              date: new Date().toString(),
              ip: json.ip,
              isp: json.isp,
              user: md.ua,
              from:
                json.location["city"] +
                ", " +
                json.location["region"] +
                ", " +
                json.location["country"],
              iplocation: json.location["lat"] + ", " + json.location["lng"],
              geolocation: "User denied the request for Geolocation."
            })
          )
          .catch(error => {
            database.push({
              date: new Date().toString(),
              user: md.ua,
              geolocation: "User denied the request for Geolocation."
            })
          })
        break
      case error.POSITION_UNAVAILABLE:
        fetch("https://geo.ipify.org/api/v1?apiKey=" + ApiKey)
          .then(res => res.json())
          .then(json =>
            database.push({
              date: new Date().toString(),
              ip: json.ip,
              isp: json.isp,
              user: md.ua,
              from:
                json.location["city"] +
                ", " +
                json.location["region"] +
                ", " +
                json.location["country"],
              iplocation: json.location["lat"] + ", " + json.location["lng"],
              geolocation: "Location information is unavailable."
            })
          )
          .catch(error => {
            database.push({
              date: new Date().toString(),
              user: md.ua,
              geolocation: "Location information is unavailable."
            })
          })
        break
      case error.TIMEOUT:
        fetch("https://geo.ipify.org/api/v1?apiKey=" + ApiKey)
          .then(res => res.json())
          .then(json =>
            database.push({
              date: new Date().toString(),
              ip: json.ip,
              isp: json.isp,
              user: md.ua,
              from:
                json.location["city"] +
                ", " +
                json.location["region"] +
                ", " +
                json.location["country"],
              iplocation: json.location["lat"] + ", " + json.location["lng"],
              geolocation: "The request to get user location timed out."
            })
          )
          .catch(error => {
            database.push({
              date: new Date().toString(),
              user: md.ua,
              geolocation: "The request to get user location timed out."
            })
          })
        break
      case error.UNKNOWN_ERROR:
        fetch("https://geo.ipify.org/api/v1?apiKey=" + ApiKey)
          .then(res => res.json())
          .then(json =>
            database.push({
              date: new Date().toString(),
              ip: json.ip,
              isp: json.isp,
              user: md.ua,
              from:
                json.location["city"] +
                ", " +
                json.location["region"] +
                ", " +
                json.location["country"],
              iplocation: json.location["lat"] + ", " + json.location["lng"],
              geolocation: "An unknown error occurred."
            })
          )
          .catch(error => {
            database.push({
              date: new Date().toString(),
              user: md.ua,
              geolocation: "An unknown error occurred."
            })
          })
        break
      default:
    }
  }

  const FbView = Firebase.database()
    .ref()
    .child("test")
    .child("view")

  FbView.once("value", snap => {
    // setView(snap.val())

    let lastView = Number(snap.val())
    let viewCount = lastView + 1
    FbView.set(viewCount)
  })

  return (
    <React.Fragment>
      <Home />
      <Aboutme />
      <Skill />
      <Project />
      <Experience />
      <Education />
      <Contact />
      <Footer />
    </React.Fragment>
  )
}

export default Pages
